/*@use postcss-cssnext;*/

html {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    
}

*, *:before, *:after {
-webkit-box-sizing: inherit;
        box-sizing: inherit;
}

main {
    display: block;
}

ul, 
li, 
dl, 
dd,
p,
a,
h1,
h2,
h3,
h4,
h5,
h6 {
    padding: 0;
    margin: 0;
}

:link:not(:hover) {
    text-decoration: none;
}

@font-face {
  font-family: 'Calluna';
  src: url('~//fonts/Calluna-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Theinhardt';
  src: url('~//fonts/TheinhardtReg.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Theinhardt';
  src: url('~//fonts/TheinhardtLight_alt.woff') format('woff');
  font-weight: lighter;
  font-style: normal;
}

@page {
  width: 31cm;
  margin: 0.5cm;
}

@media screen {

    html {
        background: #888;
        font-size: 17px;
    
    }
    
}

@media print {

    html {
        font-size: 8pt;
    
    }
    
}

body {
  font-family: 'Theinhardt', sans-serif;  
  line-height: 21.6px;  
  line-height: 1.35rem;
  padding: 0;
  color: rgba(0, 0, 0, .9);
}

.cv__grid {
    display: grid;
    max-width: 1200px;
    margin: 0;
    background: #fafcfa;
    padding: 4vw;
  }

@media (min-width: 1000px) {

    .cv__grid {
        margin: 1em auto;
        padding: 4em;
    
    }
    
}

@media print {

    .cv__grid {
        width: 21cm;
        padding: 0.5cm 2cm;
        max-width: none;
    
    }
    
}

.grid {
  margin-bottom: 1em;
  /* background-position-y: -6px;
  background-image: linear-gradient(#ddd 0, #ddd 1px, transparent 1px);
  background-size: 21.55px 21.55px; */
}

@media (min-width: 1000px),
  print {

    .grid {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 3em;
        grid-template-areas: "one two-three two-three two-three";
    
    }
    
}

.grid__header {
    grid-area: one;
    
}

.grid__main {
    grid-area: two-three;
    
}

.grid__sub {
    margin-bottom: 21.28px;
    margin-bottom: 1.33rem;
    position: relative;
    
}

.grid__sub::before {
    content: "";
    position: absolute;
    left: 0;
    top: -7.2px;
    top: -0.45rem;
    width: 100%;
    border-top: 1.2px solid #4db0c4;
    border-top: 0.075rem solid #4db0c4;
    
}

@media (min-width: 600px),
    print  {

    .grid__sub {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 3em;
        grid-template-areas:
        "two three three";
    
    }
    
}

.grid__sub-info {
    grid-area: two;
    
}

.grid__sub-description {
    grid-area: three;
    
}

.contact {
  /* @media (min-width: 800px) {
    grid-template-areas:
    "one two three";
  } */
  padding-bottom: 16px;
  padding-bottom: 1rem;
}

.contact__primary {
      grid-area: one;
  }

@media (min-width: 1000px),
      print {

    .contact__primary {
        white-space: nowrap;
        text-align: right;
    
    }
    
}

.contact__name {
    font-size: 28px;
    font-size: 1.75rem;
    
}

.contact__address {
    grid-area: two;
    
}

.contact__address-line {
    display: block;
    
}

.role__title {
    margin-bottom: 21.6px;
    margin-bottom: 1.35rem;
  }

.role__name {
    margin-bottom: 21.6px;
    margin-bottom: 1.35rem;
  }

.section-header {
  font-size: 28px;
  font-size: 1.75rem;
  font-family: 'Theinhardt', sans-serif;
  font-weight: lighter;
  white-space: nowrap;
  margin-top: -var(--header-margin-top);
  padding-bottom: 24px ;
  padding-bottom: 1.5rem ;
}

@media (min-width: 1000px),
  print {

    .section-header {
        margin-top: -var(--header-margin-top);
        text-align: right;
    
    }
    
}

.technology {
    grid-template-areas:
    "one two-three two-three two-three";
}

.technology__list {
      border-top: 1.2px solid #4db0c4;
      border-top: 0.075rem solid #4db0c4;
      grid-area: two-three;
      -webkit-column-count: 3;
              column-count: 3;
      -webkit-column-gap: 3em;
              column-gap: 3em;
    }

ul, li {
  list-style: none;
  padding-left: 0;
}

.paragraphs__p {
    font-family: 'Calluna', serif; 
    font-size: 105%
  }

.paragraphs__p:not(:first-of-type) {
    text-indent: 1em;
    
}

:link {
  color: #4db0c4;
}


